article {
	margin-top: 142px;
	@include md {
		margin-top: 50px; } }
.article {
	background: #fff;
	color: $darkblue;
	border-radius: 16px;
	padding: 80px 80px 100px 100px;
	@include md {
		padding: 40px; }
	@include sm {
		padding: 30px 10px; }
	&__heading {
		@include f(60px, 600);
		padding-bottom: 35px;
		@include md {
			@include f(48px, 600); }
		@include sm {
			text-align: center;
			@include f(36px, 600); } } }

.post {
	column-count: 2;
	column-gap: 70px;
	@include md {
		column-gap: 30px; }
	@include sm {
		column-count: 1; } }
.post__item {
	display: inline-flex;
	&:not(:last-child) {
		margin-bottom: 40px; }
	& svg {
		margin-right: 25px;
		@include md {
			margin-right: 10px; } }
	& p {
		@include f(18px, 300);
		max-width: 80%;
		margin-top: -10px;
		line-height: 2;
		@include md {
			@include f(14px, 300); }
		@include sm {
			line-height: 22px; }
		@include ms {
			max-width: 74%; } }
	@include md {
		margin-right: 0; }
	@include sm {
		max-width: none;
		width: 100%; } }
