html {
	scroll-behavior: smooth; }
body {
	margin: 0;
	font-size: 16px;
	font-family: $montserrat;
	font-weight: 400;
	color: $white;
	background-color: $navy;
	min-width: 320px; }

a {
	text-decoration: none;
	color: #fff;
	&:hover {
		text-decoration: none; } }

h1,h2,h3,h4,h5,h6 {
	margin: 0; }
p {
	margin: 0; }
ul, li {
	padding: 0;
	margin: 0;
	list-style: none; }

.out {
	overflow: hidden;
	position: relative; }

.container {
	width: 100%;
	max-width: 1230px;
	margin: 0 auto;
	padding: 0 30px;
	@include ms {
		padding: 0 5px; } }

.box {
	position: absolute;
	pointer-events: none; }
