.faq {
	position: relative;
	background: $white;
	color: $darkblue;
	border-radius: 16px;
	max-width: 1170px;
	width: 95%;
	margin: 112px auto;
	&__img {
		position: absolute;
		top: 10%;
		left: -7.7%;
		@include md {
			left: 80%;
			top: -10%; }
		@include sm {
			left: -20%; } }
	&__container {
		max-width: 1082px;
		padding: 80px;
		padding-left: 55px; }
	&__heading {
		@include f(72px, 600);
		line-height: 1;
		margin-bottom: 50px;
		& span {
			font-weight: 200;
			display: block; }
		@include md {
			@include f(48px, 600);
			max-width: 540px;
			padding: 40px;
			margin: 0; }
		@include sm {
			@include f(36px, 600); } }
	@include md {
		margin-bottom: 0; }
	@include sm {
		margin-top: 70px; } }

.accordion {
	&__item {
		display: flex;
		cursor: pointer;
		padding-bottom: 27px;
		flex-wrap: wrap;
		line-height: 1.5;
		&:last-child {
			padding-bottom: 50px; } }
	&__circle {
		@include circle(36);
		background: $navy;
		flex: none;
		position: relative;
		margin-right: 16px;
		margin-top: 5px; }
	&__arrow {
			@include vcenter;
			&_active {
				@include rotate(-90); } }
	&__question {
		@include f(30px, 600);
		max-width: 92%;
		@include md {
			@include f(24px, 600);
			max-width: 90%; }
		@include sm {
			max-width: 80%;
			@include f(18px, 600); } }
	&__answer {
		width: 100%;
		padding-top: 12px;
		padding-left: 50px;
		display: none;
		background-color: white;
		overflow: hidden;
		@include f(18px, 300);
		cursor: text;
		line-height: 2;
		@include md {
			@include f(16px, 300); }
		@include sm {
			@include f(14px, 300); } } }
