.robot {
	&_container {
		position: relative;
		@include sm {
			padding: 0 15px; }
		@include ms {
			padding: 0 5px; } }
	&__img {
		position: absolute;
		top: -135px;
		left: -40px;
		pointer-events: none;
		z-index: 1;
		@include md {
			width: 826px; }
		@include sm {
			display: none; } }
	&__heading {
		max-width: 660px;
		text-align: center;
		margin: 45px 45px 0 auto;
		line-height: 1.2;
		padding: 0 30px;
		position: relative;
		z-index: 2;
		@include md {
			max-width: 460px;
			margin-top: 80px;
			margin-right: 10%; }
		@include sm {
			margin: 60px auto 0;
			padding: 0;
			min-height: 265px; }
		& h2 {
			@include f(72px, 600);
			@include md {
				@include f(48px, 600); }
			@include sm {
				@include f(36px, 600); } }
		& p {
			@include f(30px, 600);
			margin: 7px 0 20px 13px;
			line-height: 1.6;
			@include md {
				@include f(18px, 600); } } }
	&__button {
		max-width: 320px;
		@include tr(300ms);
		@include hover {
			background: lighten($darkblue, 5%); }
		@include md {
			max-width: 260px; } }
	&__tablet-wrapper {
		width: 100%;
		background: #fff;
		border-radius: 16px;
		margin-top: 220px;
		display: flex;
		justify-content: center;
		align-items: center;
		position: relative;
		padding: 48px;
		@include md {
			margin-top: 144px;
			padding: 24px;
			max-width: 768px; }
		@include sm {
			margin-top: 246px;
			padding: 10px;
			max-width: 500px;
			left: 50%;
			transform: translateX(-50%); }
		@include ms {
			margin-top: 275px;
			max-width: 310px; } }
	&__tablet-inner {
		width: 100%;
		border-radius: 8px;
		background: $darkgray;
		display: flex;
		justify-content: center;
		align-items: center; }
	&__tablet-icon {
		@include vcenter;
		@include sm {
			& svg {
				width: 50%;
				height: 50%;
				position: relative;
				left: 50%;
				transform: translateX(-50%); } } }
	&__box1 {
		position: absolute;
		top: 6.2%;
		left: 7%;
		z-index: -2;
		@include md {
			left: -6%;
			top: 4%; }
		@include sm {
			display: none; } }
	&__box2 {
		position: absolute;
		top: 545px;
		right: 10%;
		z-index: -2;
		@include md {
			right: -4%;
			top: 340px; }
		@include sm {
			display: none; } } }
.robot__tablet-video {
	@include md {
		height: 420px; }
	@include sm {
			height: 280px; }
	@include ms {
		height: 170px; } }

.robot__img-small {
	display: none;
	@include sm {
		display: block;
		top: 10%;
		left: calc(50% - 200px);
		width: 400px; }
	@include ms {
		top: 15%; } }
