.header {
	padding: 63px 0 40px;
	@include f(16px, 600);
	@include md {
		padding: 58px 0 17px;
		@include f(14px, 600); }
	@include sm {
		padding: 22px 5px; }
	&__container {
		display: flex;
		align-items: flex-start;
		max-width: 1096px;
		@include sm {
			padding: 0 5px; } }
	&__logo {
		display: flex;
		align-items: center;
		margin-left: 35px;
		@include md {
			margin-left: 30px;
			align-items: flex-start; }
		@include sm {
			margin-left: 0;
			z-index: 4; } }
	&__img {
		margin-right: 10px;
		@include md {
			width: 32px; } }
	&__heading {
		@include f(24px, 600);
		line-height: 1;
		@include md {
			@include f(18px, 600); } }
	&__heading_colored {
		color: $lightnavy; }
	&__subheading {
		@include f(13px, 500);
		@include md {
			@include f(9.75, 500); } }
	&__nav {
		margin-left: auto;
		line-height: 2.6;
		@include md {
			line-height: 2; }
		@include sm {
			display: none; } }
	&__login {
		line-height: 2.6;
		@include md {
			line-height: 2; }
		@include sm {
			display: none; } }
	&__lang {
		margin-right: 20px;
		@include sm {
			display: none; } } }

.header__toggle {
	cursor: pointer;
	display: none;
	align-items: center;
	margin-left: auto;
	width: 24px;
	height: 24px;
	margin-right: 20px;
	@include sm {
		display: flex;
		z-index: 4; } }

.header__toggle-icon {
	position: relative;
	width: 100%;
	height: 2px;
	background-color: $white;
	&:before, &:after {
		content: '';
		position: absolute;
		width: 100%;
		height: 2px;
		left: 0;
		background-color: $white; }
	&:before {
		top: -7px; }
	&:after {
		bottom: -7px; } }

.header__toggle-icon_active {
	background-color: transparent;
	&:before {
		@include rotate(45);
		top: 0;
		background-color: $black; }
	&:after {
		@include rotate(-45);
		bottom: 0;
		background-color: $black; } }
