.news {
	margin-top: 110px;
	@include md {
		margin-top: 50px; }
	&__container {
		max-width: 1032px;
		position: relative;
		@include md {
			max-width: 860px; }
		@include sm {
			max-width: 700px; } }
	&__box {
		position: absolute;
		right: -38%;
		top: 15%;
		pointer-events: none;
		@include sm {
			display: none; } }
	&__heading {
		@include f(60px, 600);
		@include md {
			@include f(48px, 600);
			z-index: 2;
			position: relative; }
		@include sm {
			@include f(36px, 600);
			padding: 0 50px; }
		@include ms {
			padding: 0;
			text-align: center; } }
	&__button {
		margin: 30px auto;
		padding: 22px 30px;
		max-width: 320px;
		display: block;
		width: 100%;
		@include tr(300ms);
		@include hover {
			background: lighten($darkblue, 5%); }
		@include md {
			max-width: 216px;
			padding: 13px; } } }

.news-inner {
	display: flex;
	margin-top: 26px;
	@include sm {
		flex-wrap: wrap;
		justify-content: center; }
	&__item {
		margin-right: 36px;
		max-width: 300px;
		width: 100%;
		&:last-child {
			margin-right: 0; }
		@include sm {
			max-width: none;
			width: 100%;
			margin-right: 0;
			margin-bottom: 30px;
			&:last-child {
				margin-bottom: 0; } } }
	&__img {
		max-width: 300px;
		width: 100%;
		height: 300px;
		border-radius: 8px;
		background: #222;
		@include md {
			max-width: 216px;
			height: 216px; }
		@include sm {
			max-width: 290px;
			margin: 0 auto; } }
	&__heading {
		@include f(30px, 600);
		margin: 15px 0;
		line-height: 1.25;
		@include md {
			@include f(24px, 600); } }
	&__descr {
		@include f(14px, 400);
		line-height: 1.6; } }

