.heading {
	@include f(72px, 600);
	color: $darkblue;
	@include md {
		@include f(48px, 600); }
	@include sm {
		@include f(36px, 600); }
	&_white {
		color: $white; } }

.heading-small {
	@include f(36px, 600);
	@include md {
		@include f(27px, 600); } }

.subheading {
	@include f(48px, 600);
	@include md {
		@include f(36px, 600); }
	@include sm {
		@include f(30px, 600); } }
.descr {
	@include f(18px, 300);
	line-height: 2;
	&_dark {
		color: $black; }
	@include md {
		@include f(14px, 300);
		line-height: 1.6; }
	@include sm {
		@include f(14px, 300); } }
