.instructions {
	padding: 0 30px;
	@include md {
		padding-top: 50px; }
	@include sm {
		padding: 0 5px; }
	&__container {
		padding: 75px 95px 100px;
		background: $white;
		border-radius: 16px;
		max-width: 1170px;
		box-shadow: 0px 0px 30px 0px rgba(96,125,139,1);
		@include md {
			max-width: 768px;
			padding: 30px 35px; }
		@include sm {
			padding: 20px 15px; } }
	&__heading {
		@include f(60px, 600);
		max-width: 80%;
		@include md {
			max-width: none;
			line-height: 1;
			padding-right: 50px; }
		@include sm {
			padding-right: 0; } }
	&__descr {
		padding: 20px 0;
		@include md {
			padding: 25px 0; } }
	&__img {
		width: 100%;
		@include md {
			margin-bottom: 10px; } } }
