.button {
	display: inline-block;
	color: $white;
	text-decoration: none;
	background-color: $darkblue;
	padding: 17px;
	@include f(18px, 600);
	font-family: $montserrat;
	border-radius: 32px;
	text-align: center;
	outline: none;
	cursor: pointer;
	@include mwidth(370);
	&:focus, &:hover {
		text-decoration: none;
		outline: none; }
	@include md {
		@include f(16px, 600);
		max-width: 330px; }
	@include sm {
		max-width: 260px; } }


.button_white {
	color: $darkblue;
	background: $white;
	@include hover {
		color: $white;
		background: $darkblue; } }
