.token {
	color: $darkblue;
	position: relative;
	padding: 0 30px;
	@include sm {
		padding: 0 5px; }
	&__container {
		padding: 64px 95px;
		background: $white;
		border-radius: 16px;
		max-width: 1170px;
		box-shadow: 0px 0px 30px 0px rgba(96,125,139,1);
		position: relative;
		@include md {
			max-width: 768px;
			padding: 30px 35px; }
		@include sm {
			padding: 25px 25px 38px; } }
	&__box-s {
		right: 275px;
		top: -53px;
		@include md {
			right: -78px;
			top: 23px; }
		@include sm {
			display: none; } }
	&__box-b {
		left: 0;
		@include md {
			display: none; } }
	&__box-m {
		right: -215px;
		top: 170px;
		@include md {
			top: 500px; }
		@include sm {
			display: none; } }
	&__heading {
		padding-bottom: 20px;
		@include md {
			padding-bottom: 10px; } }
	&__descr {
		margin-bottom: 35px;
		@include md {
			margin-bottom: 20px; } }
	&__subheading {
		padding: 10px 0 30px;
		@include md {
			@include f(36px, 600); }
		@include sm {
			@include f(24px, 600);
			padding: 0 0 20px; } } }
