.lang {
	background: transparent;
	border-radius: 16px;
	padding: 12px;
	@include tr(200ms);
	@include hover {
		background: $white;
		.lang__current {
			color: $darkblue; }
		.lang__list {
			opacity: 1;
			visibility: visible; }
		.lang__extra {
			color: $darkblue; } }
	&__current {
		margin-bottom: 20px;
		display: inline-block;
		@include tr(200ms);
		@include hover {
			text-decoration: underline; } }
	&__list {
		opacity: 0;
		visibility: hidden;
		@include tr(200ms); }
	&__extra {
		@include hover {
			text-decoration: underline; } }
	@include md {
		padding-top: 7px; } }
