.form {
	padding-bottom: 20px;
	@include md {
		padding-bottom: 0; }
	&__input-wrapper {
		width: 100%;
		background: $lightnavy;
		border-radius: 8px;
		padding: 8px;
		margin-bottom: 50px;
		display: flex;
		align-items: center;
		@include md {
			padding: 0 0 0 8px;
			margin-bottom: 38px; }
		@include sm {
			margin-bottom: 20px; } }
	&__input {
		@include mwidth(870);
		padding: 20px 30px;
		color: $darkblue;
		@include f(24px, 800);
		@include md {
			padding: 16px 20px;
			@include f(18px, 800); }
		@include sm {
			@include f(14px, 800);
			padding: 14px 20px; } }
	&__input-btn {
		display: inline-block;
		border: none;
		outline: none;
		cursor: pointer;
		background: transparent;
		padding: 15px 27px;
		@include md {
			padding: 18px 22px; }
		@include sm {
			padding: 14px; } }
	&__button-wrapper {
		display: flex;
		@include md {
			justify-content: center; }
		@include sm {
			flex-direction: column;
			align-items: center; } }
	&__button {
		margin-right: 46px;
		margin-bottom: 20px;
		padding: 17px;
		border: 3px solid $darkblue;
		@include tr(400ms);
		@include hover {
			color: $darkblue;
			background: $white;
			@include tr(300ms); }
		&:last-child {
			margin-right: 0;
			@include md {
				text-transform: lowercase; } }
		@include md {
			margin-right: 30px; }
		@include sm {
			margin-right: 0;
			margin-bottom: 15px; } } }

.icon-copy {
	fill: none;
	stroke: $white;
	font-size: 3em;
	@include md {
		font-size: 30px; } }
