.forecast {
	margin: 140px auto 0;
	position: relative;
	&__container {
		max-width: 1080px;
		@include sm {
			padding: 0 15px; } }
	&__heading {
		@include f(60px, 600);
		padding: 0 20px 2px;
		@include md {
			@include f(48px, 600); }
		@include sm {
			@include f(30px, 600); } }
	&__desc {
		@include f(24px, 600);
		padding: 0 20px;
		@include md {
			@include f(18px, 600); }
		@include sm {
			@include f(16px, 600); } }
	&__currency {
		display: flex;
		flex-wrap: wrap;
		margin-top: 40px;
		margin-right: -12px;
		@include sm {
			margin-top: 20px; } }
	&__box1 {
		position: absolute;
		top: -65px;
		right: 7.5%;
		z-index: -1;
		@include sm {
			display: none; } }
	&__box2 {
		position: absolute;
		top: 60px;
		left: 0;
		z-index: -1;
		@include md {
			top: 1280px;
			left: -150px;
			z-index: 1; }
		@include sm {
			display: none; } }
	@include md {
		margin-top: 40px; } }

.currency {
	&__item {
		&:not(:last-child) {
			margin-right: 30px;
			margin-bottom: 30px;
			@include md {
				margin-right: 20px;
				margin-bottom: 20px; }
			@include sm {
				margin-right: 10px;
				margin-bottom: 10px; } } }
	&__link {
		display: inline-block;
		text-align: center;
		text-transform: uppercase;
		border: 8px solid #223e5c;
		background: transparent;
		min-width: 136px;
		padding: 17px 24px;
		border-radius: 8px;
		@include f(30px, 700);
		color: $white;
		transition: .3s;
		@include hover {
			background: $navy;
			border: 8px solid $navy; }
		@include md {
			@include f(18px, 700);
			min-width: 100px;
			padding: 12px; }
		@include sm {
			min-width: 90px; } } }


.graph {
	margin: 40px auto 0;
	position: relative;
	&__img {
		display: block;
		margin: 0 auto;
		position: relative;
		z-index: -2;
		width: 100%;
		padding: 0 30px; }
	@include sm {
		margin-top: 20px; } }
.indicators {
	display: flex;
	position: absolute;
	left: 27.4%;
	top: 5.5%;
	line-height: 1.3;
	&__item {
		margin-right: 45px;
		&:last-child {
			margin-right: 0; } }
	@include md {
		left: 10%; }
	@include sm {
		position: static;
		justify-content: center;
		margin-bottom: 20px; } }

.indicator {
	&__main {
		text-transform: uppercase;
		@include f(24px, 700);
		@include md {
			@include f(20px, 700); }
		@include sm {
			@include f(18px,700); }
		&_centered {
			line-height: 2.6; } }
	&__aside {
		@include f(12px, 500);
		text-align: right;
		margin-left: -15px;
		@include md {
			@include f(10px, 500); }
		& span {
			color: $green; }
		&_arrow {
			margin-bottom: 2px; } } }
