.login {
	&__list {
		display: flex; }
	&__item {
		margin-right: 30px;
		&:last-child {
			margin-right: 20px;
			@include md {
				margin-right: 10px; } }
		@include md {
			margin-right: 18px; } }
	&__link-round {
		padding: 12px 25px;
		background: $white;
		border-radius: 21px;
		color: $darkblue;
		@include md {
			padding: 9px 20px; } } }
