.contacts {
	padding-top: 50px;
	&__container {
		max-width: 1040px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		@include md {
			max-width: 780px; }
		@include sm {
			flex-wrap: wrap;
			align-items: center;
			flex-direction: column;
			padding: 0 15px; } }
	&__list {
		@include mwidth(410);
		text-align: center;
		padding: 20px;
		@include md {
			max-width: 300px; }
		@include sm {
			order: 2; } }
	&__item {
		&:not(:last-child) {
			margin-bottom: 25px; } }
	&__link {
		color: $darkblue;
		background-color: $white;
		padding: 21px;
		&_mail {
			@include f(18px, 600); }
		@include tr(400ms);
		@include hover {
			color: $white;
			background-color: $darkblue;
			.icon-telegram {
				fill: $white; } } }
	&__form {
		border: 16px solid $lightnavy;
		border-radius: 8px;
		padding: 45px 60px 55px;
		line-height: 1;
		@include md {
			padding: 30px 35px 40px;
			max-width: 380px; }
		@include sm {
			border: 12px solid $lightnavy;
			padding: 28px 28px 35px;
			margin-bottom: 30px;
			display: flex;
			flex-direction: column;
			align-items: center;
			width: 100%; } }
	&__title {
		@include f(60px, 200);
		span {
			font-weight: 600;
			display: block; }
		@include md {
			font-size: 48px; }
		@include sm {
			font-size: 36px; } }
	&__input {
		margin: 35px 0 48px;
		width: 100%;
		padding: 15px;
		background: transparent;
		border-bottom: 2px solid $lightnavy;
		@include placeholder {
			color: $white;
			@include f(24px, 600);
			@include md {
				font-size: 18px; }
			@include sm {
				font-size: 16px; } }
		@include sm {
			margin: 10px 0 25px; } }
	&__button {
		padding: 20px;
		margin: 0 auto;
		@include tr(300ms);
		@include hover {
			background: lighten($darkblue, 5%); } } }


.icon-telegram {
	fill: $darkblue;
	margin-right: 8px;
	position: relative;
	top: 4px;
	@include tr(400ms); }
